.content{
    width: 80vw;
    box-sizing: border-box;
    margin: 20px 10vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    overflow: auto;
}

.content::-webkit-scrollbar{
    display:none; /* ChromeSafari */
}

.box{
    width: calc((80vw - 80px)/5);
    min-width: 245px;
    min-height: 120px;
    border: 1px solid greenyellow;
    color: aliceblue;
    border-radius: 12px;
    margin: 7px;
    background: rgba(40,44,52,0.3);
    cursor: pointer;
}

.boxed{
    width: 80vw;
    /*height: 400px;*/
}

.text{
    text-align: left;
    margin: 5px 0 0 7px;
    font-size: 12px;
}

.title{
    margin: 7px 0 0 0;
}

.card-grid{
    border: 1px solid red;
}
